footer,
.footer {
  background: #f2f0ef;
  padding: 25px 15px;
  .logo {
    display: flex;
    align-items: center;
    @media (max-width: 768px) {
      flex-wrap: wrap;
    }
  }
  .copirait {
    font-size: 12px;
    margin-left: 10px;
    @media (max-width: 768px) {
      margin-left: 0;
      margin: 10px 0;
    }
  }
  .slogan {
    font-weight: bold;
  }
  &__description {
    display: flex;
    flex-direction: column;
    margin-left: 15px;
    margin-top: 10px;
    @media (max-width: 768px) {
      margin-left: 0;
    }
  }
  &__adress {
    font-size: 14px;
    text-align: right;
    margin-left: 20px;
  }
}
