.btn{
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  position: relative;
  cursor: pointer;
  border-radius: 3px;

  &:hover,
  &--hover{

  }

  &:active,
  &--active{

  }

  // Size modificator

  &-size-s {
    height: 35px;
    width: 175px;
    font-size: 16px;
    line-height: 31px;
  }

  &-size-m {
    height: 40px;
    width: 212px;
    font-size: 18px;
    line-height: 36px;
  }

  &-size-l {
    height: 70px;
    font-size: 18px;
    line-height: 70px;
    text-transform: uppercase;
    padding: 0 25px;
  }

  // Color modificator

  &-orange{
    background: @color-primary;
    color: @color-text-light;
    transition: all .3s ease-out;
    &:hover,
    &--hover{
      background: @color-primary--hover;
    }

    &:active,
    &--active{

    }
  }

  &-ghost-orange {
    background: transparent;
    color: @color-primary;
    border: 2px solid @color-primary;
    transition: background .3s ease-out;
    &:hover,
    &--hover{
      background: @color-primary--ghost-btn-hover;
    }
  }

  &-white{
    background: #ffffff;
    color: #ffffff;
    transition: background .3s ease-out;
    &:hover,
    &--hover{
      background: #ffffff;
    }

    &:active,
    &--active{

    }
  }

  &-ghost-white {
    line-height: 36px;
    background: transparent;
    color: #ffffff;
    border: 2px solid #ffffff;
  }

  &-ghost-gray {
    width: auto!important;
    padding: 0 10px!important;
    background: transparent!important;
    color: #8e8e8e!important;
    border: 3px solid #e9e8e7!important;
    .transition;
    &::before {
      content: '';
      display: flex;
      position: relative;
      top: 9px;
      width: 20px;
      height: 20px;
      background-image: url('/img/new-landing/svg/left-arrow.svg');
    }
    &:hover {
      border-color: #fa978a!important;
      &::before {
        background-image: url('/img/new-landing/svg/left-arrow-hover.svg');
      }
    }
  }

}
