  display: inline-block;
}
.menu li:nth-of-type(2n),
.sub-menu li:nth-of-type(2n) {
  margin: 0 0.938em;
}
.menu li.sub-list,
.sub-menu li.sub-list {
  position: relative;
}
.menu li.sub-list a,
.sub-menu li.sub-list a {
  padding-left: 15px;
  white-space: nowrap;
}
.menu li.sub-list a:hover span::before,
.sub-menu li.sub-list a:hover span::before {
  bottom: -4px;
}
.menu li.sub-list a:hover span::after,
.sub-menu li.sub-list a:hover span::after {
  top: -4px;
}
.menu li.sub-list span,
.sub-menu li.sub-list span {
  position: absolute;
  top: 35%;
  display: inline-block;
  width: 10px;
  height: 2px;
  background: #f64028;
  margin: 2px 0;
  margin-left: -15px;
}
.menu li.sub-list span::before,
.sub-menu li.sub-list span::before,
.menu li.sub-list span::after,
.sub-menu li.sub-list span::after {
  content: '';
  position: absolute;
  display: inline-block;
  width: 10px;
  height: 2px;
  background: #f64028;
}
.menu li.sub-list span::before,
.sub-menu li.sub-list span::before {
  bottom: -3px;
}
.menu li.sub-list span::after,
.sub-menu li.sub-list span::after {
  top: -3px;
}
@media (max-width: 768px) {
  .nav {
    display: none!important;
  }
}
.nav a {
  display: inline-block;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
}
.nav a:nth-of-type(2n) {
  margin: 0 0.938em;
}
@media (max-width: 768px) {
  .nav a {
    font-size: 1em;
  }
}
@media (max-width: 414px) {
  .nav a {
    font-size: 1.3em;
  }
}
.nav span {
  margin-bottom: 0.3em;
  font-size: 0.750em;
  text-transform: uppercase;
  color: #7f7f7f;
}
@media (max-width: 768px) {
  .nav {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 1.5em;
  }
}
.btn-menu {
  display: block;
  height: 30px;
  width: 35px;
  position: relative;
  z-index: 701;
  border-bottom: none;
}
.btn-menu > span {
  background-color: #f64028;
  height: 5px;
  width: 100%;
  position: absolute;
  top: 50%;
  transition: height 100ms;
}
.btn-menu > span:after,
.btn-menu > span:before {
  content: '';
  background-color: #f64028;
  height: 5px;
  width: 100%;
  position: absolute;
  transition: all 200ms;
}
.btn-menu > span:after {
  top: -10px;
}
.btn-menu > span:before {
  bottom: -10px;
}
.btn-menu.act > span {
  height: 0;
}
.btn-menu.act > span:after,
.btn-menu.act > span:before {
  background-color: #f64028;
  top: 1px;
}
.btn-menu.act > span:after {
  transform: rotate(45deg);
}
.btn-menu.act > span:before {
  transform: rotate(-45deg);
}
@media (min-width: 415px) {
  .btn-menu {
    display: none;
  }
}
.nav_mobile {
  background-color: rgba(255, 255, 255, 0.98);
  position: fixed;
  left: 0;
  top: 0;
  box-sizing: border-box;
  z-index: 700;
  height: 100%;
  width: 100%;
  display: none;
  opacity: 0;
  transition: all 0.5s ease-out;
  margin-top: 0;
}
.nav_mobile.act {
  display: flex;
  opacity: 1;
}
@media (max-width: 414px) {
  .nav_mobile.act {
    display: flex!important;
  }
}
.nav_mobile.act ul li {
  opacity: 1;
}
@media (min-width: 415px) {
  .nav_desktop {
    display: flex;
