// Top bar
.top-bar {
  width: 100%;
  padding: 10px;
  background: @color-background;
  box-sizing: border-box;
  @media (max-width: 414px) {
    & [class*="row"], & [class*="btn"] {
      width: 100%;
    }
  }

  &__phone, &__mail {
    text-decoration: none;
    font-size: 1.125em;
    font-weight: bold;
    margin-right: 20px;
    @media (max-width: 414px) {
      display: none;
    }
  }

  .col-flex-xs {
    line-height: 1em;
  }

  // top-bar grey Links
  a.link {
    color: @color-text-dark;
    &:hover {
      color: @color-primary;
    }
  }

  span {
    font-size: 0.688em;
    color: @color-text-gray-light;
    margin-right: 20px;
    @media (max-width: 768px) {
      display: none;
    }
  }
}

.header__contacts {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  @media (max-width: 768px) {
    flex-grow: 1;
    justify-content: flex-end;
    margin-right: 15px;
  }
  @media (max-width: 414px) {
    & [class*="row"], & [class*="btn"] {
      width: 100%;
    }
  }

  .btn {
    @media (max-width: 414px) {
      display: none;
    }
  }

  &__phone, &__mail {
    text-decoration: none;
    font-size: 1.125em;
    margin-right: 20px;
    @media (max-width: 414px) {
      display: none;
    }
  }

  .col-flex-xs {
    line-height: 1em;
  }

  // top-bar grey Links
  a.link {
    color: @color-text-dark;
    &:hover {
      color: @color-primary;
    }
  }

  span {
    font-size: 0.688em;
    color: @color-text-gray-light;
    margin-right: 20px;
    @media (max-width: 768px) {
      display: none;
    }
  }
}
// Top bar

// Header
header,
.header {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 100%;
  padding: 15px;
  box-sizing: border-box;
  z-index: 999;
  @media (max-width: 414px) {
  transform: none!important;
  }
  &__hr {
    margin-top: 1rem;
    margin-bottom: 1rem;
    border: 1px solid  @color-background;
  }
  .container {
    width: 100%;
  }
  &__main-text {
    h2 {
      font-size: 40px;
      text-transform: uppercase;
      font-weight: 900;
      @media (max-width: 414px) {
          font-size: 24px;
      }
    }
    @media (max-width: 768px) {
      margin-top: 40px;
    }

    .orange {
      color: @color-primary;
    }
    .price {
      color: #000;
    }

  }
  &__main-bottom {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 40%;
    bottom: 10%;
    .btn {
      &::before {
        content: '';
        display: block;
        position: absolute;
        left: 50%;
        top: 55px;
        height: 50px;
        width: 1px;
        background: @color-primary;
        .transition;
      }
      &:hover {
        &::before {
          top: 66px;
          height: 50px;
        }
      }
    }
    p {
      text-align: center;
      margin-top: 50px;
    }
    @media (max-width: 768px) {
      position: relative;
      right: 0;
      left: 0;
      width: 100%;
    }
  }
  .logo {
    z-index: 999;
  }
}


// Navigation
.nav {
  display: flex;
  flex-direction: column;
  position: relative;
}

nav {
  position: relative;
  @media (max-width: 414px) {
    width: 100%;
  }
}

.menu {
  position: relative;
  a.active {
    color: @color-primary;
    border-bottom: 2px solid @color-primary;
  }
}

// Links header

.nav__link {
  color: @color-text-dark;
  &:hover {
    color: @color-primary;
  }
}

.active {
  .nav__link {
    color: @color-primary;
  }
}

.target {
    position: sticky;
    border-bottom: 2px solid transparent;
    z-index: -1;
    transform: translateX(-60px);
    @media(max-width: 414px) {
      display: none;
    }
}

.nav__link,
.target {
    -webkit-transition: all .35s ease-in-out;
    transition: all .35s ease-in-out;
}

.body-hidden {
  overflow: hidden;
}

// Links header

@media (min-width: 414px) {
  .mobile-menu {
    display: none;
  }
}


@media (max-width: 414px) {
  .mobile-menu {
    display: flex;
    justify-content: space-between;

    a {
      text-decoration: none;
      text-transform: uppercase;
      font-weight: bold;
    }
  }

  .nav_mobile .menu {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .nav_mobile .menu li:nth-of-type(2n) {
    margin: 10px 0;
  }
}

.sub-menu {
  display: flex;
  align-items: center;
  @media (max-width: 414px) {
    overflow-x: scroll;
  }
  &_gray {
    padding: 1em;
    background: @color-background;
  }
}

.menu,
.sub-menu {
  li {
    display: inline-block;
    &:nth-of-type(2n) {
      margin: 0 0.938em;
    }
}


li.sub-list {
  position: relative;
  a {
    padding-left: 15px;
    white-space: nowrap;
    &:hover {
      span {
        &::before {
          bottom: -4px;
        }
        &::after {
          top: -4px;
        }
      }
    }
  }
  span {
    position: absolute;
    top: 35%;
    display: inline-block;
    width: 10px;
    height: 2px;
    background: @color-primary;
    margin: 2px 0;
    margin-left: -15px;
    &::before,
    &::after {
      content: '';
      position: absolute;
      display: inline-block;
      width: 10px;
      height: 2px;
      background: @color-primary;
    }
    &::before {
      bottom: -3px;
    }
    &::after {
      top: -3px;
    }
  }
}
}

.nav {
  @media (max-width: 768px) {
    display: none!important;
  }
  a {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
    &:nth-of-type(2n) {
      margin: 0 0.938em;
    }
    @media (max-width: 768px) {
      font-size: 1em;
    }
    @media (max-width: 414px) {
      font-size: 1.3em;
    }
  }
  span {
    margin-bottom: 0.3em;
    font-size: 0.750em;
    text-transform: uppercase;
    color: @color-text-gray-light;
  }
  @media (max-width: 768px) {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 1.5em;
  }
}

// menu button
.btn-menu {
  display: block;
  height: 30px;
  width: 35px;
  position: relative;
  z-index: 701;
  border-bottom: none;
> span {
  background-color: @color-primary;
  height: 5px;
  width: 100%;
  position: absolute;
  top: 50%;
  transition: height 100ms;
  &:after, &:before {
    content: '';
    background-color: @color-primary;
    height: 5px;
    width: 100%;
    position: absolute;
    transition: all 200ms;
  }
  &:after {
    top: -10px;
  }
  &:before {
    bottom: -10px;
  }
}
&.act {
  > span {
    height: 0;
    &:after, &:before {
      background-color:@color-primary;
      top: 1px;
    }
    &:after {
      transform: rotate(45deg);
    }
    &:before {
      transform: rotate(-45deg);
    }
  }
  }

  @media (min-width: 415px) {
      display: none;
  }
}




.nav_mobile {
background-color: rgba(255, 255, 255, .98);
position: fixed;
left: 0;
  top: 0;
  box-sizing: border-box;
z-index: 700;
height: 100%;
width: 100%;
  display: none;
opacity: 0;
transition: all .5s ease-out;
margin-top: 0;
&.act {
  display: flex;
  opacity: 1;
  @media (max-width: 414px) {
    display: flex!important;
  }
  ul li {
  opacity: 1;
}
}
}

.nav_desktop {
  @media (min-width: 415px) {
    display: flex;
    position: relative;
    opacity: 1;
    width: auto;
    margin-top: .3em;
    background: none;
  }
}



