

main {

}

section {
  display: flex;
  position: relative;
  padding: 2em 15px;
  overflow: hidden;
}

.container {
  width: 100%;
}

.bg-gray {
  background: @color-background;
}

.gray-fon {
  position: absolute;
  z-index: -1;
  background-image: linear-gradient(50deg,#e9e8e7,#f3f3f2);
  width: 55%;
  height: 1100px;
  right: 0;
  @media (max-width: 768px) {
    display: none;
  }
}

.no-padding {
  padding: 0;
  padding-bottom: 2em;
}


.section-main {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 650px;
  padding: 100px 25px 15px;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: cover;
  &::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    background-image: url('/img/6.jpg');
    background-position: 0% 20%;
    background-size: cover;
    margin: -100px -25px -15px;
    opacity: 0.5;
  }
  @media (max-width: 320px) {
    height: 100vh;
  }
  .container {
    width: 100%;
    flex-grow: 1;
    position: relative;
    @media (max-width: 768px) {
      align-items: flex-end;
    }
    @media (max-width: 414px) {
      align-items: flex-start;
    }
  }

  &__main-text {
    width: 100%;
    .text {
      float: right;
      p {
        font-size: 24px;
      }
      li {
        color: @color-primary;
        font-size: 18px;
        span {
          color: #000000;
        }
        &:last-child {
          font-weight: 900;
        }
      }
    }
    @media (max-width: 768px) {
      margin-top: 0;
    }
    @media (max-width: 414px) {
      margin-top: 20px;
    }
    &_h2 {
      position: absolute;
      bottom: 0;
      right: 0;
      @media (max-width: 768px) {
        bottom: 60px;
      }
      @media (max-width: 414px) {
        position: relative;
        margin-top: 0!important;
        width: 100%;
        top: 0;
      }
    }
    h1, h2 {
      font-size: 40px;
      text-transform: uppercase;
      font-weight: 900;
      @media (max-width: 768px) {
          font-size: 30px;
      }
    }
    h2 {
      display: inline-block;
      font-size: 30px;
      margin-bottom: 0.5em;
      text-align: right;
      &::before {
        display: none;
      }
      &::after {
        display: none;
      }
      @media (max-width: 414px) {
          text-align: left;
          font-size: 24px;
      }
    }
    span.price {
      color: #000;
    }

  }
}

.main-land {
  .header__main-bottom {
    bottom: 35%;
    right: 38%;
    @media (max-width: 414px) {
      margin-top: 30px;
    }
  }
}

span.orange {
  color: @color-primary;
}


section h2 {
    display: flex;
    align-items: center;
    width: 100%;
    text-transform: uppercase;
    font-size: 1.875em;
    font-weight: 900;
    @media (max-width: 414px) {
      flex-direction: row-reverse;
      justify-content: flex-end;
    }
    &::before,
    &::after {
      content: '';
      display: flex;
      flex-grow: 1;
      width: auto;
      height: 3px;
      background: @color-primary;
      margin-top: -0.188em;
    }
    &::before {
      margin-right: 10px;
    }
    &::after {
      margin-left: 10px;
    }
    @media (max-width: 768px) {
      font-size: 1.4375em;
    }
    @media (max-width: 414px) {
      margin-top: 1em!important;
    }
}

// section 1

.section-1 {
  span {
    font-size: 3em;
    font-weight: 900;
    @media (max-width: 414px) {
      font-size: 2.5em;
    }
  }
  p {
    font-size: 20px;
    line-height: 1em;
    text-align: center;
    width: 100%;
    @media (max-width: 414px) {
      font-size: 16px;
    }
  }
  .container:nth-child(2n+1) {
    .col-xs-4:nth-child(2n) {
      border-right: 3px solid @color-primary;
      border-left: 3px solid @color-primary;
    }
  }
  .container:nth-child(2n) {
    margin-top: 20px;
    border-top: 3px solid @color-primary;
    .col-xs-12 {
      margin: 20px 0;
    }
    span {
      font-size: 4em;
    }
  }
}

.section-2 {
  &__header {
    width: 100%;
    text-align: center;
    background: #f2f0ef;
    padding: 25px;
    border-radius: 6px;
    h3 {
      text-transform: uppercase;
      font-weight: 900;
    }
  }
  &__block {
    margin-top: 30px;
    h4 {
      width: 100%;
      text-align: center;
      font-size: 1.5em;
      text-align: center;
      background: #f2f0ef;
      padding: 25px;
      border-radius: 6px;
    }
    ul {
      margin: 0 60px;
    }
    li {
      font-size: 20px;
      list-style-type: decimal;
      font-weight: 900;
      &:nth-child(2n) {
        margin: 15px 0;
      }
      span {
        font-size: 16px;
        font-weight: normal;
      }
    }
    .col-xs-5 {
      @media (max-width: 414px) {
        max-width: 100%;
        flex-basis: 100%;
        &:nth-child(2n) {
          @media (max-width: 414px) {
            margin-top: 15px;
          }
        }
      }
    }
  }
}

.section-3 {
  .container {
    .container {
      margin: 30px 0;
      h4 {
        width: 100%;
        font-size: 20px;
        text-align: center;
        font-weight: 900;
        text-transform: uppercase;
        margin-bottom: 5px;
      }
      p {
        text-align: center;
      }
      .col-xs-3 {
        @media (max-width: 414px) {
          max-width: 50%;
          -ms-flex-preferred-size: 50%;
          flex-basis: 50%;
        }
        &:nth-child(3),
        &:nth-child(4) {
          @media (max-width: 414px) {
            margin-top: 15px;
          }
        }
      }
      .col-xs-6 {
        h4 {
          font-size: 30px;
          margin-bottom: 0;
        }
        p {
          font-size: 40px;
          font-weight: 900;
        }
        @media (max-width: 414px) {
          max-width: 100%;
          -ms-flex-preferred-size: 100%;
          flex-basis: 100%;
        }
      }
    }
  }
}

.video {
  h2 {
    text-align: center;
  }
  &__block {
    margin-top: 30px;
  }
}

// section 1

.boxses {
  display: flex;
  flex-wrap: wrap;
  margin: -10px;
  .box {
    display: flex;
    width: 50%;
    @media (max-width: 768px) {
      width: 100%;
    }

    &__items {
      display: flex;
      @media (max-width: 768px) {
        flex-wrap: wrap;
      }
    }
    &__item {
      padding: 25px;
      margin: 10px;
      img {
        @media (max-width: 768px) {
          width: 20%;
        }
      }
      p {
        margin-bottom: 30px;
        @media (max-width: 768px) {
          margin-bottom: 0;
        }
      }
      &_grey {
        background: #f2f0ef;
        align-items: flex-start;
        @media (max-width: 768px) {
          flex-wrap: wrap;
        }
      }
      &_white {
        padding-bottom: 0;
        p {
          text-align: center;
        }
      }
      &_text {
        width: 40%;
        @media (max-width: 768px) {
          width: 100%;
        }
      }
      &_slider {
        width: 100%;
        @media (max-width: 768px) {
          display: none;
        }
      }
    }
    &__text {
      margin-left: 30px;
      @media (max-width: 768px) {
        margin-left: 0;
        margin-top: 20px;
      }
      h4 {
        font-size: 16px;
        text-transform: uppercase;
        font-weight: 900;
      }
    }
    .btn {
      &::before {
        content: '';
        display: block;
        position: absolute;
        left: 50%;
        top: -25px;
        height: 35px;
        width: 1px;
        background: @color-primary;
        .transition;
      }
      &:hover {
        &::before {
          top: -30px;
          height: 30px;
        }
      }
    }
    .btn-size-l {
      @media (max-width: 768px) {
        height: auto;
        line-height: normal;
        padding: 15px 25px;
        margin-top: 30px;
      }
    }
  }
  &__item {
    flex-wrap: nowrap;
    @media (max-width: 768px) {
      flex-wrap: wrap;
    }
  }
}

.box__item_slider {
  width: 100%;
}

.items-card {
  display: flex;
  flex-direction: column-reverse;
  width: 100%;
  &-text {
    flex-wrap: nowrap;
    margin-top: 30px;
    @media (max-width: 768px) {
      flex-wrap: wrap;
      padding-right: 0;
    }
    p {
      margin-bottom: 30px;
      padding-right: 80px;
      @media (max-width: 768px) {
        padding-right: 0;
      }
    }
    a {
      width: 100%;
      @media (max-width: 768px) {
