// modal
.modal {
    position: fixed;
    display: flex;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s 0s, visibility 0s 0.3s;
    z-index: 999;
    @media (min-width: 768px) {
        top: 0;
        overflow-y: auto;
        background: rgba(0,0,0,.6);
    }
}

.modal.is-visible {
    opacity: 1;
    visibility: visible;
    -webkit-transition: opacity 0.3s 0s, visibility 0s 0s;
    -moz-transition: opacity 0.3s 0s, visibility 0s 0s;
    transition: opacity 0.3s 0s, visibility 0s 0s;
}

.modal__inner {
    width: 100%;
    height: 100%;
    margin: auto;
    padding: 45px 45px 45px;
    background-color: #fff;
    box-sizing:border-box;
    @media (min-width: 768px) {
        width: 600px;
        height: auto;
    }
    @media (max-width: 414px) {
      padding: 15px 15px 45px;
    }
}

.modal__wrapper {
    display: flex;
    flex-direction:column;
    justify-content:flex-start;
    position: relative;
    min-height: 100%;
    h4 {
        font-size: 24px;
        line-height: 24px;
        font-weight: 900;
        text-transform: uppercase;
        text-align:center;
        margin-top: 0;
        margin-bottom: 10px;
        @media (max-width:414px) {
            font-size:20px;
            line-height:20px;
        }
    }
}

.header-description {
  text-align: center;
  margin-bottom: 20px;
}

.modal__form input[type=tel],
.modal__form input[type=text],
.modal__form input[type=email] {
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 10px;
    outline: none;
    margin: 0;
}

.modal__form input[required] + label {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin-top: -4px;
    right: 20px;
}

.modal__form input[required] + label:before  {
  content: '';
  display: block;
  height: 5px;
  width: 5px;
  background: @color-primary;
}

.modal__form input[type=submit] {
    width: 100%;
    box-sizing: border-box;
    border: none;
}

.modal__form .required-block {
  position: relative;
}

.modal__form .required {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center;
  &::before {
    content: '';
    display: block;
    height: 5px;
    width: 5px;
    background: @color-primary;
    margin-right: 10px;
  }
}

.modal__form .description {
    display: block;
    font-size: 12px;
    width: 100%;
    text-align: center;
    color: #939090;
    margin-top: 20px;
}

.modal__form .bottom-bn {
    max-width: 100%;
}




.close-wrap{
    display: flex;
    z-index: 1111;
    text-align:right;
    margin-bottom: 35px;
}

.close-bn{
    position: relative;
    width: 100%;
    height: 45px;
    cursor: pointer;
    background: #eee;
    border-radius: 5px;
    &:hover {
      &::before,
      &::after {
        background: #000;
      }
    }
}

.close-bn:before,
.close-bn:after {
    content: "";
    position: absolute;
    top: 21px;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 26px;
    height: 4px;
    background: #999;
    .transition;
}

.close-bn:before {
    webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.close-bn:after {
    webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

.form-send {
    position: relative;

    &__success {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align:center;
        background: #ffffff;
        z-index: 99;
        font-size: 24px;
        transition: .25s;
        margin-top: -5px;
        .description {
          margin-bottom: 20px;
        }
        .lk-lp {
          display: flex;
          flex-direction: column;
          width: 100%;
          background: #eee;
          padding: 20px 25px;
          border-radius: 5px;
          box-sizing: border-box;
          span {
            color: #555;
            span {
              color: #000;
            }
          }
        }
        @media (max-width: 414px) {
            font-size:18px;
        }
        &_hidden {
            opacity: 0;
            visibility: hidden;
        }

        &_visible {
            opacity: 1;
            visibility: visible;
        }
        .btn {
          margin-top: 30px;
        }
        .btn-lk {
          width: 100%;
          line-height: 70px;
