        }
    }

    .bottom-bn {
        font-size: 24px;
        margin-top: 15px;
        &_thanks {
            max-width: 300px;
        }
    }
}

// swiper

.slider {
  display: block;
  position: relative;
  width: 100%;
  margin-top: 20px;
  @media (max-width: 768px) {
    padding-bottom: 0!important;
  }
}

.sw-block {
  width: 360px;
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
  &:active {
    cursor: grabbing;
    cursor: -moz-grabbing;
    cursor: -webkit-grabbing;
  }
  img {
    width: 100%;
    height: 100%;
  }
}

.swiper-container {
      width: 100%;
      height: 100%;
      padding-bottom: 24px!important;
      box-sizing: border-box;
    }
    .swiper-slide {
      text-align: center;
      font-size: 18px;
      /* Center slide text vertically */
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
    }

.swiper-button-next,
.swiper-button-prev {
  width: 9px!important;
  height: 17px!important;
  background-size: 9px 17px!important;
}

.swiper-pagination-bullet-active {
  background: @color-primary!important;
}

.swiper-pagination-bullet {
  border-radius: 0!important;
}

.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 0!important;
}

.swiper-button-next {
  background-image: url('/img/svg/arrow-right.svg')!important;
}

.swiper-button-prev {
  background-image: url('/img/svg/arrow-left.svg')!important;
}

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background: #f64028!important;
}

.comments .slider .swiper-wrapper,
.section-1 .slider .sw-block,
.comments .comments__box .swiper-container  {
  border-radius: 6px;
  overflow: hidden;
}
.comments .comments__box .swiper-container  {
    padding-bottom: 0!important;
}
.comments .slider .swiper-wrapper {
  padding-bottom: 0!important;
  @media (max-width: 414px) {
    overflow: visible;
  }
}


