.comments__box {
 .container {
   width: 67%;
 }
 .twentytwenty-container img {
   width: 100%;
 }
}

.comment {
  display: flex;
  position: fixed;
  opacity: 0;
  overflow: hidden;
  .transition;
  @media (max-width: 768px) {
    flex-wrap: wrap;
  }

  &__block {
    display: flex;
    flex-direction: column;
    width: 40%;
    margin-left: 35px;
    margin-bottom: -40px;
    @media (max-width: 768px) {
      width: 100%;
      margin-left: 0;
      margin-top: 50px;
    }
    &::before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: 75%;
      height: 500px;
      display: block;
      background-image: linear-gradient(50deg,#e9e8e7,#f3f3f2);
      border-radius: 6px 0 0 6px;
      z-index: -2;
      @media (max-width: 414px) {
        width: 95%;
        height: 600px;
      }
    }
    .text {
      margin-bottom: 50px;
      @media (max-width: 414px) {
        margin-left: 30px;
      }
      @media (max-width: 320px) {
        margin-left: 25px;
      }
      b {
        font-size: 20px;
        font-weight: 900;
      }
      p {
        position: relative;
        margin-top: 15px;
        @media (max-width: 414px) {
          left: 0;
          margin-top: 10px;
        }
      }
    }
    .plan {
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;
      height: 170px;
      top: -25px;
      @media (max-width: 320px) {
        height: 200px;
      }
      h4 {
        text-transform: uppercase;
        font-size: 1.1em;
        margin-bottom: 0.5em;
      }
      h4,span {
        font-weight: 900;
      }
      p {
        @media (max-width: 320px) {
          margin-right: 10px;
        }
      }
      &::before {
        content: '';
        position: absolute;
        right: 0;
        left: -70px;
        width: 100%;
        height: 170px;
        display: block;
        background-image: url(/img/new-landing/svg/plan.svg);
        background-position: 98% 10px;
        background-color: #fff;
        box-shadow: 0 0 25px #ccc;
        background-repeat: no-repeat;
        box-shadow: 0 0 25px #ccc;
        border-radius: 6px;
        z-index: -2;
        @media (max-width: 414px) {
          left: -20px;
          padding-left: 20px;
        }
        @media (max-width: 320px) {
          height: 200px;
        }
      }
    }
  }
}

.comment.comment-active {
  position: static;
  opacity: 1;
  overflow: visible;
}

.calculator {
  background-image: linear-gradient(50deg,#e9e8e7,#f3f3f2);
}



form.calc {
  position: relative;
  width: 100%;
  min-height: 450px;
  padding: 25px;
  background: #fff;
  border-radius: 6px;
  @media (max-width: 414px) {
    padding: 0;
    background: none;
  }
  img {
    position: absolute;
    left: -120px;
    top: -35px;
    @media (max-width: 768px) {
      display: none;
    }
  }
  .steps {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-left: 300px;
    @media (max-width: 768px) {
      margin: 0;
    }
    .step {
      display: none;
      flex-wrap: wrap;
      justify-content: flex-end;
      position: relative;
      &-1 {display: flex;}
      &-2 {
        .input-block {
          @media (max-width: 414px) {
            flex-wrap: nowrap;
            align-items: flex-end;
          }
        }
      }
      &-3 {
        .input-block {
          @media (max-width: 414px) {
            flex-wrap: wrap;
          }
          .input {
            @media (max-width: 414px) {
              margin-bottom: 10px;
            }
            .spans {
              span.label {
                @media (max-width: 414px) {
                  margin-bottom: 0;
                  padding-right: 10px;
                }
              }
            }
          }
        }

      }
      &-active {
        display: flex;
      }
      .progress {
        width: 100%;
        span {
          font-size: 18px;
          font-weight: bold;
          color: @color-primary;
        }
        .bar {
          position: relative;
          height: 7px;
          width: 100%;
          background: #dedede;
          border-radius: 5px;
          margin: 1em 0;
          &::before {
            content: '';
            position: absolute;
            height: 7px;
            background: @color-primary;
            border-radius: 5px;
          }
          &-1 {
            &::before {
              width: 20%;
            }
          }
          &-2 {
            &::before {
              width: 40%;
            }
          }
          &-3 {
            &::before {
              width: 60%;
            }
          }
          &-4 {
            &::before {
              width: 80%;
            }
          }
          &-5 {
            &::before {
              width: 100%;
            }
          }
        }
      }
      span {
        font-size: 22px;
        font-weight: bold;
      }
      span.step-title {
        margin: 1em 0 0.5em 0;
        width: 100%;
      }
      .nov,
      .vtor {
        flex-grow: 1;
        border-radius: 5px;
        p {
          font-size: 14px;
          color: #949494;
          margin-left: 37px;
          @media (max-width: 414px) {
            margin: 0;
          }
        }
      }
      .vtor {
        margin-left: 6px;
        @media (max-width: 414px) {
          margin: 0;
          margin-top: 6px;
        }
      }
      .input-block,
      .bottoms-block {
        display: flex;
        width: 100%;
        justify-content: flex-end;
        align-items: center;
        .back-arrow {
          display: flex;
          position: relative;
          top: 10px;
          left: 0;
          width: 20px;
        }
      }

      .input {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        flex-grow: 1;
        width: 100%;
        &:nth-of-type(2n) {
          margin-left: 10px;
          @media (max-width: 414px) {
            margin: 0;
          }
        }
        .spans {
          display: flex;
          justify-content: space-between;
          width: 100%;
          position: relative;
          padding: 0 10px;
          box-sizing: border-box;
          span.label,
          span.metr2 {
            background: #ffffff;
            z-index: 1;
            @media (max-width: 414px) {
              background: #efeeed;
            }
          }
          span.label {
            margin-bottom: 10px;
            line-height: 1em;
            text-align: center;
            font-size: 14px;
          }
          span.metr2 {
            padding-left: 5px;
          }
          span.dot {
            top: 14px;
            width: 95%;
            @media (max-width: 414px) {
              display: flex!important;
              width: 90%;
            }
          }
        }
        .counter {
          display: flex;
          @media (max-width: 414px) {
            flex-direction: column-reverse;
          }
          &__border {
            padding: 10px;
            border: 3px solid #e9eae8;
            border-radius: 6px;
          }
          input[type='number'] {
              width: 50px;
              text-align: center;
          }
          input::-webkit-outer-spin-button,
          input::-webkit-inner-spin-button {
              -webkit-appearance: none;
          }
          .but {
            width: 30px;
            background: #e9e9e8;
            border: 3px solid #e9e9e8;
            border-radius: 5px;
            font-weight: 900;
            color: #f64028;
            @media (max-width: 414px) {
              background: #ffffff;
            }
            &:hover {
              border-color: #dedede;
            }
            &:nth-child(1) {
              margin-right: 3px;
              margin-left: 0!important;
              @media (max-width: 414px) {
                width: 100%;
                margin-right: 0;
                margin-top: 3px;
              }
            }
            &:nth-child(2n+1) {
              margin-left: 3px;
              @media (max-width: 414px) {
                width: 100%;
                margin-left: 0;
                margin-bottom: 3px;
              }
            }
          }
        }
        span {
          font-size: 18px;
        }
        input[type=number] {
          width: 100%;
          height: 55px;
          font-size: 20px;
          font-weight: 900;
          box-sizing: border-box;
          border: 3px solid #e9e8e7;
          outline: none;
          border-radius: 5px;
          &:hover {
            border-color: #dedede;
          }
        }
        &__field {
          position: relative;
          span.metr2 {
            position: absolute;
            top: 6px;
            right: 6px;
          }
        }
      }
      .next-step,
      .final-step {
        width: 250px;
        height: 45px;
        padding: 10px;
        font-size: 14px;
        color: #fff;
        border: none;
        text-transform: uppercase;
        outline: none;
        -webkit-appearance: none;
        @media (max-width: 414px) {
          width: 100%;
        }
      }
      .next-step {
        display: inline-block;
        padding: 0;
        line-height: 47px;
        text-align: center;
      }
      .final-step {
        width: 270px;
        padding: 0;
        line-height: 47px;
        @media (max-width: 414px) {
          width: 100%;
          margin-top: 10px;
        }
      }
      .disabled {
          background: #dedede;
          color: #8e8e8e;
          cursor: default;
      }
      span.dot {
        position: absolute;
        top: 16px;
        width: 100%;
        border-bottom: 1px dotted #bbb;
        z-index: 0;
        @media (max-width: 414px) {
          display: none;
        }
      }
    }

    .step-1-1,
    .step-2,
    .step-3,
    .step-4 {
      .btn-orange {
        margin-left: 10px;
        @media (max-width: 414px) {
          &[data-class=".step-start"] {
            margin-left: 0;
          }
        }
      }
    }
    .step-2{
      .spans {
        justify-content: center!important;
      }
      .input:nth-of-type(2n) {
        margin-right: 10px;
        @media (max-width: 414px) {
          margin-right: 0;
        }
      }
      .counter{
        input:disabled,
        button:disabled {
          background: #dedede!important;
          color: #7d7d7d!important;
          border-color: #dedede!important;
        }
      }
    }
    .step-2,
    .step-3 {
      .input-block:nth-of-type(2n) {
        margin-bottom: 20px;
      }
    }
    .step-4 {
      .calc-types:nth-of-type(2n) {
        margin: 10px 0;
      }
      span.name {
        font-size: 18px;
        line-height: 1em;
      }
    }
    .step-5 {
      .step__price {
        display: flex;
        flex-direction: column;
        span.description {
          display: flex;
          justify-content: space-between;
          position: relative;
          font-size: 20px;
          @media (max-width: 414px) {
            flex-wrap: wrap;
          }
          span.title {
            z-index: 1;
            padding-right: 10px;
            background: #ffffff;
            font-size: 20px;
            @media (max-width: 414px) {
              font-size: 18px;
              background: none;
              padding: 0;
            }
          }
          span#total-price {
            font-size: 24px;
            font-weight: 900;
            background: #ffffff;
            padding-left: 10px;
            z-index: 1;
            @media (max-width: 414px) {
              font-size: 45px;
              background: none;
              padding: 0;
            }
          }
          span.price {
            background: #ffffff;
            z-index: 1;
            @media (max-width: 414px) {
              background: none;
            }
          }
          span.rub {
            @media (max-width: 414px) {
              font-size: 45px;
              margin-left: 10px;
            }
          }
        }
        p {
          font-size: 14px;
          color: #949494;
          margin-top: 15px;
          @media (max-width: 414px) {
            font-size: 12px;
            margin-top: 0;
          }
        }
      }
      span.smeta {
        font-size: 20px;
      }
      .bottom-bn {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        @media (max-width: 414px) {
          flex-wrap: wrap;
        }
        span {
          line-height: 1.2em;
          text-transform: uppercase;
          font-weight: 900;
        }
      }
    }
  }
  h2 {
    margin-top: 20px!important;
    @media (max-width: 414px) {
      margin-top: 0!important;
    }
    &::before {
      display: none;
    }
  }
  .bottom-bn {
    display: flex;
    justify-content: flex-end;
    margin-top: 50px;
    @media (max-width: 414px) {
      margin-top: 10px;
    }
  }
}

.calc-steps {
  width: 100%;
}

.calc-types {
  display: flex;
  @media (max-width: 414px) {
    flex-wrap: wrap;
  }
}
//

.calc__type_checked {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px 15px;
  border: 3px solid #e9e8e7;
  border-radius: 5px;
  box-sizing: border-box;
  .transition;
  @media (max-width: 414px) {
    background: #ffffff;
  }
  .name {
    display: flex;
    position: relative;
    &::before {
      content: '';
      min-width: 20px;
      height: 20px;
      border: 3px solid #e9e8e7;
      border-radius: 50%;
      margin-right: 10px;
    }
  }
  &:hover {
    background: #fbfbfb;
  }
}

.calc__type input[type=radio],
.calc__type input[type=checkbox]
{
    display: none;
}
input[type=radio]:checked + .calc__type_checked,
input[type=checkbox]:checked + .calc__type_checked
{
    border-color: @color-primary;
    .name {
      &::before {
        border-color: lighten(@color-primary,20%);
      }
      &::after {
        content: '';
        position: absolute;
        top: 6px;
        left: 6px;
        width: 14px;
        height: 14px;
        background: @color-primary;
        border-radius: 50%;
      }
    }
}


