        font-weight: 900;
        @media (max-width: 414px) {
          font-size: 24px;
        }
      }
      a {
        font-size: 24px;
        font-weight: 900;
      }
      p {
        @media (max-width: 414px) {
          font-size: 14px;
        }
      }
    }
  }
}

